.background-Photo {
  position: absolute;
  top: 1%;
  background-color: #d3c6bd;
  display:flex;
  flex-direction: row;
  justify-content:center;
  width: 100%;
  z-index: 0;
}

.topFixedBar{
  display:flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 70px;
  top: 0;
  background-color: #EBE1D6;
  list-style: none;
  border-bottom: 2px solid #000000;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-left: 2px solid #000000;
  border-right: 2px solid #000000;
  z-index: 1;
  font-size: 16px;
  font-family: 'Inter', Arial, Helvetica, sans-serif;
}

.topFixedBar .logo{
  position: fixed;
  width: 4%;
  left: 5%;
}

.topFixedBar li{
  display: inline;
  margin-right: 150px;
  text-decoration: underline;
  color: #0E4866;
  &:hover{
    font-size: 18px;
    color: #2d1f45;
    cursor: pointer;
  }
}
.login-button{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  color: #0E4866;
  text-decoration: underline;
  &:hover{
    font-size: 18px;
    color:#2d1f45;
    cursor: pointer;
  }
  
}

.background-down-button {
  display:flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 70px;
  height: 70px;
  left: 50%;
  bottom: 5%;
  background-color: #ebe1d691;
  border-radius: 50px;
  z-index: 1;
}
.down-button{
  width: 60%;
  &:hover{
    cursor: pointer;
    width: 65%;
  }
}

.lateral-bar{
  display:flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  list-style: none;
  position: fixed;
  top: 29%;
  left: 1.3%;
  z-index: 1;
}
.lateral-bar li{
  width: 35px;
  height: 35px;
  background-color: #276346;
  border: 2.5px solid #C98354;
  border-radius: 50%;
  margin-bottom: 70px;
  &:hover{
    background-color: #0e4866;
    cursor: pointer;
  }
}